.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 10px;
  display: grid;
}

.photo-card {
  text-align: center;
  border: 1px solid #ccc;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 10px;
  display: grid;
}

.info-item {
  display: grid;
}

.search-form {
  text-align: center;
  margin: 20px;
}

.search-input {
  height: 40px;
  border: 1px solid #0a3f2d;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #033718;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

